import { useEffect, useState } from "react";

// generic(매개타입) - 함수를 호출하는 곳에서 타입 결정
const usePagination = <T>(countPerPage: number) => {
  //          state: 전체 객체 리스트 상태          //
  const [totalList, setTotalList] = useState<T[]>([]);
  //          state: 보여줄 객체 리스트 상태          //
  const [viewList, setViewList] = useState<T[]>([]);
  //          state: 현재 페이지 번호 상태          //
  const [currentPage, setCurrentPage] = useState<number>(1);

  //          state: 전체 페이지 번호 상태          //
  const [totalPageList, setTotalPageList] = useState<number[]>([1]);
  //          state: 보여줄 페이지 번호 상태          //
  const [viewPageList, setViewPageList] = useState<number[]>([1]);

  //          state: 현재 섹션 상태          //
  const [currentSection, setCurrentSection] = useState<number>(1);
  //          state: 전체 섹션 상태          //
  const [totalSection, setTotalSection] = useState<number>(1);

  //          function : 보여줄 객체 리스트 추출 함수          //
  const setView = () => {
    const FIRST_INDEX = countPerPage * (currentPage - 1);
    const LAST_INDEX =
      totalList.length > countPerPage * currentPage
        ? countPerPage * currentPage
        : totalList.length;
    const viewList = totalList.slice(FIRST_INDEX, LAST_INDEX);

    setViewList(viewList);
  };

  //          function : 보여줄 페이지 리스트 추출 함수          //
  const setViewPage = () => {
    const FIRST_INDEX = 10 * (currentSection - 1);
    const LAST_INDEX =
      totalPageList.length > 10 * currentSection
        ? 10 * currentSection
        : totalPageList.length;
    const viewPageList = totalPageList.slice(FIRST_INDEX, LAST_INDEX);

    setViewPageList(viewPageList);
  };

  //          effect : total list가 변경될 때 마다 실행할 작업          //
  useEffect(() => {
    // console.log("totalList", totalList);
    // console.log("totalList.length", totalList.length);
    const totalPage = Math.ceil(totalList.length / countPerPage);
    const totalPageList: number[] = [];

    for (let index = 1; index <= totalPage; index++) {
      totalPageList.push(index);
    }

    console.log(totalPageList);

    setTotalPageList(totalPageList);

    const totalSection = Math.ceil(totalList.length / (countPerPage * 10));
    setTotalSection(totalSection);

    setCurrentPage(1);
    setCurrentSection(1);

    setView();
    setViewPage();
  }, [totalList]);

  //          effect : current page 변경될 때 마다 실행할 작업          //
  useEffect(setView, [currentPage]);

  //          effect : current section 변경될 때 마다 실행할 작업          //
  useEffect(setViewPage, [currentSection]);

  return {
    currentPage, // 현재 페이지
    setCurrentPage,
    currentSection, // 현재 섹션
    setCurrentSection,
    viewList, // 보여줄 리스트
    viewPageList, // 보여줄 페이지 리스트
    totalSection, // 전체 섹션
    setTotalList,
  };
};

export default usePagination;
